const URL_CIRCLE = 'https://i.imgur.com/EpFWMQl.png';
const URL_MIDO = 'https://i.imgur.com/cNvWq7Z.png';

const URL = URL_MIDO;
export type t_loading = 'LOADING' | 'SUCCESS' | 'FAILED' | 'IDLING';
export default function Product() {
	const [state_text, set_text] = React.useState<string>(TESTTEXTL);
	const [state_bgImageLoading, set_bgImageLoading] =
		React.useState<t_loading>('IDLING');
	const [state_bgLocalFileBase64, set_bgLocalFileBase64] =
		React.useState<string>(); //使い道なくなったが、クッキーで使えるかも？
	const [state_bgUrl, set_bgUrl] = React.useState<string>();
	const [state_bgElement, set_bgElement] = React.useState<HTMLImageElement>();
	const [state_bgImageModeListIndex, set_bgImageModeListIndex] =
		React.useState<number>(0);
	const [state_bgOpacity, set_bgOpacity] = React.useState<number>(1);
	const [state_bgColor, set_bgColor] = React.useState<string>('#ffffff');
	const installedFontFamilies: string[] = React.useMemo(
		() => flatJoin(',', filterInstalledFont(FONT_FAMILIES)),
		[],
	);
	const [state_fontFamily, set_fontFamily] = React.useState<string>(
		installedFontFamilies[0],
	);
	const [state_fontSize, set_fontSize] = React.useState<number>(28);
	const [state_fontColor, set_fontColor] = React.useState<string>('#000000');
	/*
	const [state_fontColor, set_fontColor] = React.useState<string>(
		FONT_COLORS[0],
	);
	*/
	const [state_fontStrokeColorListIndex, set_fontStrokeColorListIndex] =
		React.useState<number>(0);
	const [state_lineHeight, set_lineHeight] = React.useState<number>(1.5);
	//const [state_origImageW, set_origImageW] = React.useState<number>(100);
	//const [state_origImageH, set_origImageH] = React.useState<number>(100);
	const [state_stageWidth, set_stageWidth] = React.useState<number>(0);
	const ref_stage = React.useRef<Konva.Stage>(null); //画像化に使う
	//const ref_image = React.useRef<Konva.Image>(null);
	const [debouncedText] = useDebounce(state_text, 1000);
	const debouncedSetBg = useDebouncedCallback(
		// function
		(url: string) => {
			setBgImageData(url);
		},
		// delay in ms
		777,
	);
	React.useEffect(() => {
		const windowWidth = window.innerWidth;
		set_stageWidth(
			DEFAULT_STAGE_MAX_WIDTH > windowWidth //ウィンドウサイズがDEFAULT_STAGE_MAX_WIDTHよりも小さければ、ウィンドウサイズ-5%をステ―ジのデフォのサイズにする。
				? windowWidth * 0.95
				: DEFAULT_STAGE_MAX_WIDTH,
		);
	}, []);

	const onFontSizeChange = React.useCallback(
		(value: string) => set_fontSize(Number(value)),
		[],
	);

	/**
	 * 画像が読み込まれた後に実行されるので実質これが終わった時が読み込み終了時
	 * @param url
	 * @returns
	 */
	const setBgImageData = async (url: string) => {
		console.log({ url: url });
		if (!url || url === '') {
			set_bgElement(undefined);
			set_bgImageLoading('IDLING');
			return;
		}
		set_bgImageLoading('LOADING');
		const res = await loadImage(url).catch((e) => {
			console.log({ url_loading_failed: e });
			return;
		});
		if (!res) {
			set_bgElement(undefined);
			set_bgImageLoading('FAILED');
			return;
		}
		set_bgElement(res);
		set_bgImageLoading('SUCCESS');
	};

	const updateBgUrl = React.useCallback((value: string) => {
		set_bgLocalFileBase64(undefined);
		const url = commandToUrl(value);
		console.log({ urlaaaaaaa: url });
		set_bgUrl(url);
		debouncedSetBg(url);
	}, []);

	const updateBgBae64 = React.useCallback((value: string) => {
		set_bgUrl(undefined);
		set_bgLocalFileBase64(value);
		debouncedSetBg(value);
	}, []);

	/*
	const fontStrokeColorValue = React.useMemo(
		() =>
			R.pipe(
				R.find<t_fontStrokeColor>(R.propEq('name', state_fontStrokeColorName)),
				R.defaultTo(FONT_STROKE_COLORS[0]),
				R.prop('value'),
			)(FONT_STROKE_COLORS),
		[state_fontStrokeColorName],
	);
*/

	return (
		<Box mb={4}>
			<Box sx={{ display: 'flex', justifyContent: 'center' }}>
				<Typography gutterBottom variant='h6' component='h1'>
					文章を画像に変換
				</Typography>
			</Box>
			<Routes>
				<Route
					path='/'
					element={
						<ScreenHome
							propsTextForm={{ value: state_text, onChange: set_text }}
						/>
					}
				/>
				<Route
					path='/edit'
					element={
						<ScreenEdit
							bgImageLoading={state_bgImageLoading}
							propsBgImageSelectTabs={{
								url: state_bgUrl,
								onUrlChange: updateBgUrl,
								onFileImageLoaded: updateBgBae64,
							}}
							propsBgImageModeRadioButton={{
								items: BG_IMAGE_MODES,
								onChange: set_bgImageModeListIndex,
								selectedItemIndex: state_bgImageModeListIndex,
							}}
							propsBgOpacitySlider={{
								value: state_bgOpacity,
								onChange: set_bgOpacity,
								marks: BG_OPACITY_SLIDER_MARKS,
							}}
							propsBgColorInput={{
								value: state_bgColor,
								onInput: set_bgColor,
							}}
							propsFontFamilySelector={{
								items: installedFontFamilies,
								value: state_fontFamily,
								onChange: set_fontFamily,
							}}
							propsFontSizeSelector={{
								items: FONT_SIZE_LIST,
								value: String(state_fontSize),
								onChange: onFontSizeChange,
							}}
							propsFontColorInput={{
								value: state_fontColor,
								onInput: set_fontColor,
							}}
							/*
							propsColorPicker={{
								colorList: FONT_COLORS,
								selectedColor: state_fontColor,
								onChange: set_fontColor,
							}}
							*/
							propsFontStrokeColorRadioButton={{
								items: FONT_STROKE_COLORS,
								onChange: set_fontStrokeColorListIndex,
								selectedItemIndex: state_fontStrokeColorListIndex,
							}}
							propsLineHeightForm={{
								value: state_lineHeight,
								onChange: set_lineHeight,
							}}
							propsStageWidth={{
								value: state_stageWidth,
								onChange: set_stageWidth,
							}}
							propsCanvas={{
								fontFamily: state_fontFamily,
								fontSize: state_fontSize,
								fontColor: state_fontColor,
								fontStrokeColor:
									FONT_STROKE_COLORS[state_fontStrokeColorListIndex].value,
								lineHeight: state_lineHeight,
								stageWidth: state_stageWidth,
								text: debouncedText,
								bgImage: state_bgElement,
								bgOpacity: state_bgOpacity,
								bgColor: state_bgColor,
								ref_stage: ref_stage,
								mode: BG_IMAGE_MODES[state_bgImageModeListIndex].value,
								//ref_image: ref_image,
							}}
						/>
					}
				/>
			</Routes>
		</Box>
	);
}
